.consultation {
  background-repeat: no-repeat;
  background-position: center bottom;
  background-color: #f1f3f5;
  @include adaptive-value("padding-top", 90, 25, 1);
  @include adaptive-value("padding-bottom", 90, 25, 1);
  @include adaptive-value("margin-bottom", 150, 45, 1);
  text-align: center;

  display: flex;
  align-items: center;
  justify-content: center;
}

// .consultation__inner {}

.consultation__title {
  max-width: 840px;
  width: 100%;
  margin-bottom: 65px;
}

.consultation__form {
  margin-bottom: 25px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  justify-items: center;
  grid-gap: 20px;
}

.consultation__input {
  background-color: $white;
  box-shadow: 0px 5px 14px #e4e4e4;
  border-radius: 4px;
  border: 1px solid transparent;
  color: rgba(57, 57, 57, 0.8);
  padding: 16px;
  max-width: 260px;
  width: 100%;
}

.consultation__button {
  &:focus {
    box-shadow: 0px 0px 0px 3px rgba($black, 0.5);
  }

  &:hover {
    color: $blue-900;
    background-color: transparent;
    border: 1px solid rgba($blue-900, 0.9);
  }

  &:active {
    color: rgba($blue-900, 0.5);
    box-shadow: inset 0px 4px 4px rgba($orange, 0.25);
  }
}

.consultation__text {
  font-size: 14px;
  color: rgba(57, 57, 57, 0.8);
}
